.about-us {
    @include default_content;

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 768px) {
            flex-direction: column;
        }
    }

    &__gallery {
        margin: -10px;
        width: 468px;
        display: grid;
        grid-template-columns: 50% 50%;

        @media (max-width: 1024px) {
            width: 50%;
        }

        @media (max-width: 900px) {
            width: 30%;
            grid-template-columns: 100%;
        }

        @media (max-width: 768px) {
            width: 100%;
            grid-template-columns: 33.3334% 33.3334% 33.3334%;
        }
    }

    &__header {
        margin-bottom: 64px;
        text-align: center;

        @media (max-width: 900px) {
            margin-bottom: 32px;
        }

        @media (max-width: 768px) {
            margin-bottom: 24px;
        }   
    }

    &__text {
        padding-right: 54px;
        width: calc(100% - 468px);

        @media (max-width: 1024px) {
            width: 50%;
        }

        @media (max-width: 900px) {
            padding-right: 32px;
            width: 70%;
        }

        @media (max-width: 768px) {
            padding-right: 0;
            padding-bottom: 24px;
            width: 100%;
        }
    }

    &__picture {
        position: relative;
        margin: 10px;
        overflow: hidden;

        & > img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
        }

        &::before {
            display: block;
            content: '';
            width: 100%;
            padding-bottom: 100%;
        }

        &:nth-child(1),
        &:nth-child(3) {
            grid-column: 1;
            grid-row: 1/3;

            @media (max-width: 768px) {
                grid-row: 1;
                grid-column: 1/3;
            }
        }

        &:nth-child(2),
        &:nth-child(4) {
            grid-column: 2;
            grid-row: 1/3;

            @media (max-width: 900px) {
                display: none;
            }

            @media (max-width: 768px) {
                display: block;
                grid-column: 1/2;
                grid-row: 2;
            }
        }

        &:nth-child(3) {
            grid-row: 3/4;

            @media (max-width: 768px) {
                grid-column: 3/4;
                grid-row: 1;
            }
        }

        &:nth-child(4) {
            grid-row: 2/4;

            @media (max-width: 768px) {
                grid-column: 2/4;
                grid-row: 2;
            }
        }

        &:nth-child(1),
        &:nth-child(4) {
            &::before {
                padding-bottom: 150%;

                @media (max-width: 768px) {
                    padding-bottom: 0;
                    padding-left: 150%;
                }
            }
        }
    }
}