.banner {
    position: relative;
    height: calc(100vh - 220px);

    @media (max-width: 768px) {
        height: 56.5vh;
    }

    @media (max-width: 578px) {
        height: calc(100vh - 220px);
    }

    @media (max-width: 526px) {
        height: 100vh;
    }

    .swiper {
        width: 100%;
        height: 100%;
    }
    
    .swiper-slide {
        font-size: 18px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;

        @media (max-width: 578px) {
            padding: 10px;
        }

        &__info {
            margin-left: auto;
            padding: 0 32px;
            width: 50%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            background-color: #fff;

            @media (max-width: 900px) {
                width: 70%;
            }

            @media (max-width: 768px) {
                padding: 16px;
                width: 50%;
            }

            @media (max-width: 578px) {
                width: 100%;
                height: 100%;
            }
        }

        &__title {
            margin-bottom: 24px;

            @media (max-width: 768px) {
                margin-bottom: 16px;
            }
        }
        
        &__text {
            margin-top: 32px;
            font-size: 18px;
            line-height: 24px;

            @media (max-width: 768px) {
                margin-top: 24px;
            }

            @media (max-width: 768px) {
                font-size: 16px;
                line-height: 22px;
            }
        }

        &__text,
        &__title,
        &__subtitle {
            text-align: center;
        }
    }

    .swiper-pagination {
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-width: 900px) {
            width: 60%;
        }

        @media (max-width: 768px) {
            width: 100%;
        }

        @media (max-width: 578px) {
            padding-bottom: 8px;
        }
    }

    .swiper-page {
        position: relative;
        width: 20px;
        height: 20px;
        font-size: 14px;
        line-height: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 600;
        color: #fff;

        @media (max-width: 578px) {
            color: #000;
        }

        &::before {
            position: absolute;
            right: -12px;
            width: 10px;
            height: 1px;
            display: block;
            content: '';
            background-color: #fff;

            @media (max-width: 578px) {
                background-color: #000;
            }
        }

        & + .swiper-page {
            margin-left: 24px;
            color: #000;

            &::before {
                right: unset;
                left: -12px;
                background-color: #000;
            }
        }
    }
    
    .parallax-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 130%;
        height: 100%;
        -webkit-background-size: cover;
        background-size: cover;
        background-position: center;
    }
}