.subheader {
    padding: 12px 32px;
    height: 90px;
    min-height: 90px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    line-height: 22px;

    @media (max-width: 768px) {
        padding: 12px 16px;
        height: inherit;
        min-height: inherit;
    }

    @media (max-width: 578px) {
        padding: 8px 10px;
    }

    @media (max-width: 526px) {
        flex-direction: column-reverse;
    }

    @media (max-width: 340px) {
        flex-direction: column;
    }

    &:hover {
        .subheader__name {
            width: 96px;
        }
    }

    &__links {
        display: flex;
        align-items: center;

        @media (max-width: 340px) {
            flex-direction: column;
        }
    }

    &__contasts {
        margin-right: 18px;
        display: flex;
        flex-direction: column;

        @media (max-width: 340px) {
            margin-right: 0;
        }
    }

    &__address {
        @media (max-width: 340px) {
            margin:8px 0;
        }
    }

    &__name,
    &__symbol {
        &.icon {
            width: 36px;
            height: 36px;

            @media (max-width: 578px) {
                width: 30px;
                height: 30px;
            }
        }
    }

    &__name {
        &.icon {
            width: 0;
            transition: all .3s ease-in-out;

            @media (max-width: 1024px) {   
                width: 96px;

                @media (max-width: 578px) {   
                    width: 80px;
                }
            }
        }
    }

    &__link-mail,
    &__link-phone,
    &__link-location {
        display: flex;
        align-items: center;

        .icon {
            margin-right: 8px;
        }
    }

    &__logo {
        display: flex;
        justify-content: flex-end;

        @media (max-width: 526px) {
            margin-bottom: 8px;
        }

        @media (max-width: 340px) {
            margin-bottom: 0;
        }
    }

    &__form-anchor {
        display: flex;
        justify-content: flex-end;
    }
}