.header {
    padding: 12px 32px;
    height: 62px;
    min-height: 62px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #ededed;
    font-size: 16px;
    line-height: 22px;

    @media (max-width: 526px) {
        align-items: flex-start;
        justify-content: center;
    }

    @media (max-width: 768px) {
        padding: 12px 16px;
        height: inherit;
        min-height: inherit;
    }

    @media (max-width: 578px) {
        padding: 8px 10px;
    }

    &__link {
        @media (max-width: 526px) {
            width: 262px;
            transform: translate(-144px);
            text-align: center;
        }

        & + .header__link {
            margin-left: 16px;

            @media (max-width: 526px) {
                margin-left: 0;
                margin-bottom: 6px;
            }
        }
    }

    &__nav {
        display: flex;
        align-items: center;

        @media (max-width: 526px) {
            width: 118px;
            flex-direction: column-reverse;
            align-items: flex-start;
        }
    }

    &__language {
        margin-left: 24px;
        display: flex;
        align-items: center;

        @media (max-width: 768px) {
            margin-left: 12px;
        }

        @media (max-width: 526px) {
            margin-left: 12;
            margin-bottom: 12px;
        }
    }

    &__lang {
        padding: 6px;
        border: 1px solid #29434e;
        color: #29434e;
        font-size: 18px;
        line-height: 24px;
        text-transform: uppercase;
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-width: 768px) {
            padding: 4px;
            font-size: 16px;
            line-height: 22px;
        }

        @media (max-width: 526px) {
            padding: 7px 12px;
        }

        &.active {
            background-color: #29434E;
            color: #ffffff;
        }

        & + .header__lang {
            border-left: none;
        }
    }
}