@mixin default_content {
    p,
    ul,
    ol {
        font-size: 18px;
        line-height: 24px;

        @media (max-width: 768px) {
            font-size: 16px;
            line-height: 22px;
        }

        @media (max-width: 578px) {
            font-size: 14px;
            line-height: 20px;
        }
    }

    p {
        & + p,
        & + ul,
        & + ol {
            margin-top: 16px;

            @media (max-width: 768px) {
                margin-top: 10px;
            }
        }
    }

    ul {
        padding-left: 14px;
        
        & > li {
            list-style-type: 'â€”';
            padding-left: 12px;

            &::marker {
                color: #29434E;
            }
        }
    }

    ul,
    ol {
        & > li {
            & + li {
                margin-top: 10px;
            }
        }
    }
}