.footer {
    padding: 12px 32px;
    height: 62px;
    min-height: 62px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #ededed;
    font-size: 16px;
    line-height: 22px;

    @media (max-width: 768px) {
        padding: 12px 16px;
        height: inherit;
        min-height: inherit;
    }

    @media (max-width: 578px) {
        padding: 8px 10px;
    }

    &__link,
    &__link-mail,
    &__link-phone {
        margin-left: 18px;
        display: flex;
        align-items: center;

        .icon {
            margin-right: 8px;
        }
    }

    &__contasts,
    &__social-media {
        margin-left: -18px;
        display: flex;
    }

    &__contasts {
        @media (max-width: 900px) {
            flex-wrap: wrap;
        }
    }
}