.worth {
    @include default_content;
    position: relative;

    @media (max-width: 578px) {
        padding: 10px;
    }

    &__wrap {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
    }

    &__img {
        width: calc(50% + 32px);
        position: relative;
        margin-top: -96px;
        margin-left: -32px;
        margin-bottom: -96px;

        & > img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @media (max-width: 900px) {
            width: calc(30% + 32px);
            margin-top: -48px;
            margin-bottom: -48px;
        }

        @media (max-width: 768px) {
            margin-top: -24px;
            margin-bottom: -24px;
            margin-left: -16px;
            width: calc(50% + 16px);
        }

        @media (max-width: 578px) {
            position: absolute;
            z-index: 1;
            margin: -10px -10px 0;
            width: 100%;
            height: 100%;
        }
    }

    &__subheader {
        margin-bottom: 32px;

        @media (max-width: 768px) {
            margin-bottom: 24px;
        }
    }

    &__header {
        text-align: center;
        margin-bottom: 24px;

        @media (max-width: 768px) {
            margin-bottom: 16px;
        }
    }

    &__text {
        padding-left: 54px;
        width: 50%;

        li > .h5 {
            margin-bottom: 6px;
        }

        @media (max-width: 900px) {
            width: 70%;
        }

        @media (max-width: 768px) {
            padding-left: 24px;
            width: 50%;
        }

        @media (max-width: 578px) {
            position: relative;
            z-index: 2;
            padding: 16px;
            width: 100%;
            background-color: #ededed;
        }
    }
}