.field {
    padding: 6px 12px;
    color: #111;
    background-color: #fff;
    font-size: 18px;
    line-height: 32px;

    @media (max-width: 768px) {
        padding: 4px 8px;
        font-size: 16px;
        line-height: 22px;
    }

    & + .field {
        margin-top: 32px;

        @media (max-width: 768px) {
            margin-top: 16px;
        }
    }

    &--textarea {
        min-height: 160px;
        resize: none;

        @media (max-width: 768px) {
            min-height: 120px;
        }
    }
}