.contact {
    @include default_content;

    &__wrap {
        border: solid 3px #29434E;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 578px) {
            flex-direction: column;
        }
    }

    &__text {
        padding: 24px 32px;
        text-align: center;

        @media (max-width: 900px) {
            padding: 32px 24px;
        }

        @media (max-width: 768px) {
            padding: 24px 16px;
        }
    }

    &__form,
    &__text {
        width: 50%;

        @media (max-width: 578px) {
            width: 100%;
        }
    }

    &__form {
        margin: -1px 0;
        padding: 64px 32px;
        background: #29434e;
        display: flex;
        flex-direction: column;

        @media (max-width: 900px) {
            padding: 32px 24px;
        }

        @media (max-width: 768px) {
            padding: 24px 16px;
        }

        @media (max-width: 578px) {
            margin: -1px;
            width: calc(100% + 2px);
        }
    }

    &__header {
        margin-bottom: 64px;
        text-align: center;

        @media (max-width: 900px) {
            margin-bottom: 32px;
        }
    }

    &__form-btn {
        margin-top: 52px;
        align-self: center;

        @media (max-width: 768px) {
            margin-top: 32px;
        }
    }
}