.h1 {
    font-size: 46px;
    line-height: 46px;

    @media (max-width: 1024px) {
        font-size: 42px;
        line-height: 42px;
    }

    @media (max-width: 768px) {
        font-size: 38px;
        line-height: 38px;
    }

    @media (max-width: 578px) {
        font-size: 32px;
        line-height: 32px;
    }
}

.h2 {
    font-size: 40px;
    line-height: 40px;

    @media (max-width: 1024px) {
        font-size: 36px;
        line-height: 36px;
    }

    @media (max-width: 768px) {
        font-size: 32px;
        line-height: 32px;
    }

    @media (max-width: 578px) {
        font-size: 28px;
        line-height: 28px;
    }
}

.h3 {
    font-size: 34px;
    line-height: 34px;

    @media (max-width: 1024px) {
        font-size: 30px;
        line-height: 30px;
    }

    @media (max-width: 768px) {
        font-size: 26px;
        line-height: 26px;
    }

    @media (max-width: 578px) {
        font-size: 22px;
        line-height: 22px;
    }
}

.h4 {
    font-size: 28px;
    line-height: 28px;

    @media (max-width: 1024px) {
        font-size: 24px;
        line-height: 24px;
    }

    @media (max-width: 768px) {
        font-size: 20px;
        line-height: 20px;
    }
}

.h5 {
    font-size: 22px;
    line-height: 22px;

    @media (max-width: 1024px) {
        font-size: 18px;
        line-height: 18px;
    }

    @media (max-width: 768px) {
        font-size: 14px;
        line-height: 14px;
    }
}

.h6 {
    font-size: 16px;
    line-height: 16px;

    @media (max-width: 1024px) {
        font-size: 12px;
        line-height: 12px;
    }

    @media (max-width: 768px) {
        font-size: 10px;
        line-height: 10px;
    }
}