* {
    padding: 0;
    margin: 0;
    text-decoration: none;
    border: none;
    color: inherit;
    background: none;
    box-sizing: border-box;
    list-style-type: none;
    font-family: 'Didact Gothic', sans-serif;
    outline: none;

    @media (max-width: 768px) {
        font-size: 14px;
        line-height: 20px;
    }

    @media (max-width: 578px) {
        font-size: 12px;
        line-height: 18px;
    }
}

a {
    cursor: pointer;
    transition: all .3s ease-in-out;

    &:hover {
        opacity: 0.7;
    }
}

.section {
    padding: 96px 32px;

    @media (max-width: 900px) {
        padding: 48px 32px;
    }

    @media (max-width: 768px) {
        padding: 24px 16px;
    }

    @media (max-width: 578px) {
        padding: 16px 10px;
    }

    &--gray {
        background-color: #ededed;
    }
}

.main-header {
    position: absolute;
    top: -100%;
    left: -100%;
    visibility: hidden;
    height: 0;
    width: 0;
}

